* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none;
}

:root {
  color: rgb(56, 52, 52);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  margin-left: 50px;
  margin-top: 50px;
}

.title {
  font-size: 40px;
  margin-bottom: 20px;
}


.input{
  font-size: 18px;
  padding: 10px;
  margin:10px 0;
  border: 2px solid #eee;
  border-radius:10px;

}

.button {
  background-color: #f02372;
  border: none;
  color: white;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
}
